// next-app/components/CommentList.js
import { formatDistanceToNow } from 'date-fns'; // You'll need to install date-fns: npm install date-fns
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';

export default function CommentList({ comments }) {
  if (!comments || comments.length === 0) {
    return <p className="text-gray-500 italic mt-4">No comments yet. Be the first to comment!</p>;
  }

  return (
    <div className="mt-6 bg-white p-4 rounded-lg shadow-sm">
      <h3 className="text-xl font-bold mb-4 text-gray-800">Comments ({comments.length})</h3>
      <ul className="space-y-4">
        {comments.map(comment => (
          <li key={comment._id} className="border-b border-gray-200 pb-4 last:border-b-0">
            {/* You'll need to fetch author details for comments from your API if not already included */}
            <div className="flex items-center text-sm text-gray-600 mb-1">
              <span className="font-semibold text-blue-700">{comment.author?.username || 'Anonymous'}</span>
              <span className="mx-2">•</span>
              <span>{formatDistanceToNow(new Date(comment.createdAt), { addSuffix: true })}</span>
            </div>
            <ReactMarkdown remarkPlugins={[remarkGfm]}>{comment.contentMarkdown}</ReactMarkdown>
            {/* Placeholder for comment upvotes/replies */}
            {/* <div className="flex items-center text-sm mt-2">
              <button className="text-gray-500 hover:text-blue-600 mr-3">↑</button>
              <span className="font-semibold text-gray-700">0</span>
              <button className="text-gray-500 hover:text-red-600 ml-3">↓</button>
              <span className="ml-4 text-blue-500 hover:underline cursor-pointer">Reply</span>
            </div> */}
          </li>
        ))}
      </ul>
    </div>
  );
}