// next-app/pages/post/[id].js
import CommentList from '../../components/CommentList';
import CommentForm from '../../components/CommentForm';
import { useRouter } from 'next/router';
import { useEffect, useState } from 'react';
import axios from '../../services/api'; // Ensure axios is imported
import { formatDistanceToNow } from 'date-fns'; // For displaying post creation time
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';

export default function PostDetail() {
  const router = useRouter();
  const { id } = router.query;
  const [post, setPost] = useState(null);
  const [comments, setComments] = useState([]);
  const [loadingPost, setLoadingPost] = useState(true);
  const [loadingComments, setLoadingComments] = useState(true);
  const [errorPost, setErrorPost] = useState(null);
  const [errorComments, setErrorComments] = useState(null);

  useEffect(() => {
    if (id) {
      const fetchPost = async () => {
        try {
          const res = await axios.get(`/posts/${id}`);
          setPost(res.data);
        } catch (err) {
          console.error('Error fetching post:', err);
          setErrorPost('Failed to load post. It might have been deleted or does not exist.');
        } finally {
          setLoadingPost(false);
        }
      };

      const fetchComments = async () => {
        try {
          const res = await axios.get(`/comments/${id}`);
          setComments(res.data);
        } catch (err) {
          console.error('Error fetching comments:', err);
          setErrorComments('Failed to load comments.');
        } finally {
          setLoadingComments(false);
        }
      };

      fetchPost();
      fetchComments();
    }
  }, [id]);

  if (loadingPost) return <p className="text-center py-8 text-lg text-gray-600">Loading post details...</p>;
  if (errorPost) return <p className="text-center py-8 text-lg text-red-600">{errorPost}</p>;
  if (!post) return <p className="text-center py-8 text-lg text-gray-500">Post not found.</p>;


  return (
    <div className="p-4 max-w-3xl mx-auto bg-white rounded-lg shadow-md border border-gray-200 mt-8">
      {/* Post Header */}
      <div className="border-b pb-4 mb-6">
        <h1 className="text-3xl font-extrabold mb-2 text-gray-900 leading-tight">{post.title}</h1>
        <div className="flex items-center text-sm text-gray-600">
          <span className="font-semibold text-gray-700">Posted by u/{post.author?.username || 'Anonymous'}</span>
          <span className="mx-2">•</span>
          <span>{formatDistanceToNow(new Date(post.createdAt), { addSuffix: true })}</span>
        </div>
      </div>

      {/* Post Content */}
      <div className="prose max-w-none mb-8 text-gray-800 leading-relaxed"> {/* Use 'prose' for better markdown rendering if react-markdown is used */}
      <ReactMarkdown remarkPlugins={[remarkGfm]}>
          {post.contentMarkdown}
      </ReactMarkdown>
      </div>

      {/* Comments Section */}
      <h3 className="text-2xl font-bold mb-4 text-gray-800 border-b pb-2">Comments</h3>
      {loadingComments ? (
        <p className="text-gray-500 italic">Loading comments...</p>
      ) : errorComments ? (
        <p className="text-red-600 italic">{errorComments}</p>
      ) : (
        <CommentList comments={comments} />
      )}

      <CommentForm postId={id} onCommentAdded={comment => setComments(prev => [comment, ...prev])} /> {/* Add new comments to the top */}
    </div>
  );
}